import uniIcon from "@/components/m-icon/m-icon.vue";

export default {
	components: {
		uniIcon
	},


	data() {
		return {
			tabCurrentIndex: 0,
			status: 0,
			navList: [{
					value: 0,
					text: '建站',
					orderList: []
				},
				{
					value: 1,
					text: '求租',
					orderList: []
				},
				{
					value: 2,
					text: '设计',
					orderList: []
				},
				{
					value: 3,
					text: '注册公司',
					orderList: []
				},
				{
					value: 4,
					text: '高佣金',
					orderList: []
				}
			],
			selected: '无限',
			mySelect: [{
					value: 0,
					text: '无限'
				},
				{
					value: 1,
					text: '100元以下'
				},
				{
					value: 2,
					text: '100~2000元'
				},
				{
					value: 3,
					text: '2500~4000元'
				},
				{
					value: 4,
					text: '4000元以上'
				}
			],
			isActive: false,
			isActive2: true,
			selectaA: false
		};

	},

	onLoad(options) {
		// 页面显示是默认选中第一个
		this.tabCurrentIndex = 0;
	},

	methods: {
		changeTab(e) {
			this.tabCurrentIndex = e.target.current;
		},
		//顶部tab点击
		tabClick(index) {
			this.tabCurrentIndex = index;
		},
		show() {
			if (this.status == 1) {
				this.status = 0
			} else {
				this.status = 1
			}
		},
		finish() {
			if (this.checked = 'checked') {
				this.isActive = true;
				this.isActive2 = false;
			}

		},
		unfinish() {
			if (this.checked = 'checked') {
				this.isActive2 = true;
				this.isActive = false;
			}
		},
		// 提交
		formSubmit: function(e) {
			console.log('form发生了submit事件，携带数据为：' + JSON.stringify(e.detail.value))
			var formdata = e.detail.value
			uni.showModal({
				content: '表单数据内容：' + this.isActive,
				showCancel: false
			});
		},
		formReset: function(e) {
			this.selected=this.mySelect[0].text; //重置

		},
		Myselect(i) {
			this.selected = this.mySelect[i].text;
			this.selectaA = false
		},
		showSelectA() {
			if (this.selectaA == true) {
				this.selectaA = false
			} else {
				this.selectaA = true;
			}
		}
	},

};
